import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { connect } from "react-redux";
import { setRestaurantInfo } from '../../redux/reducers/userReducer';

function RestaurantAuth(props) {
  const [auth, setAuth] = useState({
    email: '',
    password: ''
  });

  const handleChange = (type, e) => {
    console.log('e', e);
    auth[type] = e;
    let currentRestauran = { ...props.restaurant };
    currentRestauran[type] = e;
    props.dispatch(setRestaurantInfo(currentRestauran));
    setAuth(auth);
  }

  return (
    <div>
      <TextField
        variant="outlined"
        margin="normal"
        required
        fullWidth
        id="email"
        label="Email Address"
        name="email"
        autoComplete="email"
        autoFocus
        onChange={e => handleChange('email', e.target.value)}
      />
      <TextField
        variant="outlined"
        margin="normal"
        required
        fullWidth
        name="password"
        label="Password"
        type="password"
        id="password"
        autoComplete="current-password"
        onChange={e => handleChange('password', e.target.value)}
      />
    </div>
  )
}

const mapStateToProps = ({ userReducer }) => {
  return {
    restaurant: userReducer.restaurant
  }
}

export default connect(mapStateToProps)(RestaurantAuth);