import axios from "axios";

export function getReservationService(page, limit) {
  return axios.get(`https://eatbana.herokuapp.com/api/reservations?page=${page}&limit=${limit}`);
}

export function updateStatusReservation(id, body) {
  return axios.put(`https://eatbana.herokuapp.com/api/reservations/status/${id}`, body);
}